.count {
    width: 140px;
    height: 40px;
    background-color: #eeeaea;
    border-radius: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;


    & .plus {
        align-items: center;
        background-color: #0e7490;
        width: 30px;
        height: 40px;
        color: white;
        text-align: center;
        justify-content: center;
        position: absolute;
        user-select: none;
        font-size: 23px;
        font-weight: bold;
        display: flex;
        left: 0px;
        border-bottom-left-radius: 50px;
        border-top-left-radius: 50px;
        cursor: pointer;
    }

    & .muns {
        align-items: center;
        background-color: #0e7490;
        width: 30px;
        height: 40px;
        user-select: none;
        color: white;
        text-align: center;
        position: absolute;
        font-size: 23px;
        font-weight: bold;
        justify-content: center;
        display: flex;
        right: 0px;
        border-bottom-right-radius: 50px;
        border-top-right-radius: 50px;
        cursor: pointer;
    }
}


.fa {
    width: 20px;
    position: absolute;
    top: -3px;
    right: 2px;
    background-color: red;
    color: white;
    height: 20px;
    border-radius: 50%;
    display: flex;
    font-size: 12px;
    justify-content: center;
    align-items: center;
}


.wwww {
    background-color: white !important;
    border: 1px solid white !important;
}