.cars {
    position: relative;
    height: calc(100vh - 62px);

    img {

        height: 100%;
        object-fit: cover;
        width: 100%;
    }

    button {
        position: relative;
        z-index: 999;
    }

    .main {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        z-index: 9;
        width: 100%;
        height: 100%;
        background-color: #00000059;
        display: flex;
        align-items: center;
        justify-content: center;
    }

}