.cars {
  position: relative;
  height: calc(100vh - 62px);
}
.cars img {
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  width: 100%;
}
.cars button {
  position: relative;
  z-index: 999;
}
.cars .main {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 9;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3490196078);
  display: flex;
  align-items: center;
  justify-content: center;
}/*# sourceMappingURL=style.css.map */